* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

img { 
  height: 200px;
  width: 100%;
}

input {
  font-size: 1.12em;
  height: 46px;
  border: 4px solid #e4e4e4;
  transition: border-color .4s;
}

input:focus {
  border-color: #c1c1c1;
}

button {
  height: 46px;
  cursor: pointer;
}

